import app from 'flarum/app';

app.initializers.add('fof/hCaptcha', () => {
    app.extensionData
        .for('vbgamer45-hCaptcha')
        .registerSetting({
            label: app.translator.trans('vbgamer45-hCaptcha.admin.settings.help_text', {
                a: <a href="http://www.google.com/hCaptcha/admin" target="_blank" />,
            }),
            type: 'hidden',
        })
        .registerSetting({
            setting: 'vbgamer45-hCaptcha.type',
            label: app.translator.trans('vbgamer45-hCaptcha.admin.settings.type_label'),
            options: {
                checkbox: 'Checkbox',
                invisible: 'Invisible',
            },
            required: true,
            default: 'checkbox',
            type: 'select',
        })
        .registerSetting({
            setting: 'vbgamer45-hCaptcha.credentials.site',
            label: app.translator.trans('vbgamer45-hCaptcha.admin.settings.site_key_label'),
            type: 'text',
            required: true,
        })
        .registerSetting({
            setting: 'vbgamer45-hCaptcha.credentials.secret',
            label: app.translator.trans('vbgamer45-hCaptcha.admin.settings.secret_key_label'),
            type: 'password',
            required: true,
        })
        .registerPermission(
            {
                permission: 'vbgamer45-hCaptcha.postWithoutCaptcha',
                label: app.translator.trans('vbgamer45-hCaptcha.admin.permissions.post_without_captcha'),
                icon: 'fas fa-robot',
            },
            'reply'
        );
});
